<template>
  <div id="app">
    <p>{{initValue}}</p>
    <p>{{updateValue}}</p>
    <l-editor :initValue="initValue" @confirm="receiver"></l-editor>
  </div>
</template>

<script>
import lEditor from './components/latex-editor'

export default {
  data () {
    return {
      initValue: 'init',
      updateValue: ''
    };
  },
  methods: {
    receiver (value) {
      this.updateValue = value;
    }
  },
  components: {
    lEditor
  }
}
</script>

<style>
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px; */
}
</style>



