<script>
// latexEditor.setWhiteList(reg)
var WHITE_LIST = /[0-9a-zA-Z\u4e00-\u9fa5%\\()[\]{}|^_/*+-<>=!.&,':$ ]+/;
function _inputRule (latex) {
  return latex
    .replace(/\\sum\\limits_/g, '\\sum_')
    .replace(/\\backsim/g, '');
}
function _outputRule (latex) {
  return latex
    .replace(/\\sum_/g, '\\sum\\limits_')
    .replace(//g, '\\backsim ');
}

import '../assets/jquery'
import '../assets/mathquill'
import formulaSet from './formulaList'
export default {
  mounted () {
    this.jax = document.getElementById('latexEditorPreview')
    this.init()
  },
  props: {
    initValue: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      dialogVisible: false,
      value: '',
      mqEditor: null,
      formulaSet: formulaSet,
      currentGroupIndex: -1,
      currentLabelIndex: -1,
      subject: 'math',
      latexModel: false,
      jax: null,
      historyList: [],
      isHiddenTips: false,
      onlyLatexKeyList: [12, 15, 21, 42, 633, 634, 635, 636, 73, 75, 78, 79,
        135, 136, 146, 147, 148, 149, 155, 157, 158, 159, 160, 161, 162, 163, 1671,// 10
        169, 171, 175, 176,// 11
        178, 179, 180, 181, 182, 183, 189, 190, 191,// 12
        // 193, 194, 196, 197,// 13
        208, 209, 210, 211, 214, 215, 216, 217, 218, 220,// 14
        221, 225, 226, 227, 228, 229, 230, 231, 232,// 15
        233, 234, 235, 236, 237,// 16
        256, 257, 258, 260, 262, 263, 264, 266, 267, 268, 269, 270, 272, 273, 274, 275, 277, 278, 279, 280, 281, 283, 284, 285,// 18
        288, 291, 292, 293, 295, 296, 297, 298, 299, 300, 301// 19
      ]
    }
  },
  methods: {
    init () {
      window.latexEditor = {
        set: (latex) => {
          this.setValue(latex)
        },
        setWhiteList: (reg) => {
          WHITE_LIST = reg;
        }
      }
      let value = this.initValue || '';
      this.mqInit()
      this.setValue(value)
      this.updateHistory(value);
    },
    showDialog () {
      this.dialogVisible = true;
    },
    setValue (latex) {
      // this.value_inputRule
      this.value = latex;
      this.mqEditor.latex(_inputRule(latex));
      this.mqEditor.focus();
      this.$nextTick(function () {
        this.jax.innerText = '$' + _outputRule(this.value) + '$';
        MathJax.Hub.Queue(['Typeset', MathJax.Hub, '_YkLatexEditor']);
      });
    },
    mqInit () {
      var MQ = MathQuill.getInterface(2);
      var answerSpan = document.getElementById('latexEditArea');
      this.mqEditor = MQ.MathField(answerSpan, {
        handlers: {
          edit: () => {
            let latex = this.mqEditor.latex();
            if (this.value === latex) return;
            this.setThisValue(latex);
            this.jax.innerText = '$' + _outputRule(this.value) + '$';
            MathJax.Hub.Queue(['Typeset', MathJax.Hub, '_YkLatexEditor']);
            this.updateHistory(this.value);
          }
        }
      })
    },
    toogleLatex () {
      this.latexModel = !this.latexModel;
      this.$nextTick(function () {
        if (!this.latexModel) {
          this.mqEditor.latex(_inputRule(this.value));
          this.mqEditor.focus();
        } else {
          this.value = _outputRule(this.value);
          this.textarea.focus();
        }
      });
    },
    insert (latex, key) {
      if (this.latexModel) {
        var pos = this.getSelectedText(this.textarea);
        //  input 
        this.value = this.value.substring(0, pos.start) + latex + ' ' + this.value.substring(pos.end);
        this.updateMqJax();
        this.setCaretPosition(this.textarea, pos.start + latex.length + 1);
      } else {
        if (this.onlyLatexKeyList.indexOf(key) !== -1) {
          this.latexModel = true;
          this.$nextTick(function () {
            this.insert(latex);
          });
          return;
        }
        this.mqEditor.write(_inputRule(latex));
        this.mqEditor.focus();
      }
      this.currentGroupIndex = this.currentLabelIndex = -1;
    },
    confirm () {
      let output = {
        latex: _outputRule(this.value),
        svg: this.jax.querySelector('svg').outerHTML
      };
      console.log(output);
      this.$emit('confirm', output);
      latexEditor.get && latexEditor.get(output);
    },
    textareaInput (event) {
      // enter()
      if (event.inputType === 'insertLineBreak') {
        return;
      }
      this.setThisValue(event.target.value);
      this.updateMqJax();
    },
    setThisValue (value) {
      // 
      if (this.value.length > value.length) {
        this.value = value;
      } else {
        // 
        let obj = this.filterValue(value);
        if (!obj.move) {
          this.value = obj.value;
        } else {
          this.textarea.blur();
          // 
          if (!this.latexModel) {
            // mathquill
            this.value = obj.value;
            this.mqEditor.latex(obj.value);
            this.mqEditor.moveToRightEnd();
            return;
          }
          let posStart;
          t: for (let i = 0; i < value.length; i++) {
            if (value[i] !== this.value[i]) {
              posStart = i;
              break t;
            }
          }
          let len = this.value.length;
          // console.log(`old: ${this.value}:${this.value.length}, new: ${value}:${value.length}, posStart: ${posStart}`);
          // 
          this.value = obj.value;
          let that = this;
          setTimeout(function () {
            if (posStart !== len) {
              //  1  2
              // that.setCaretPosition(that.textarea, posStart + obj.move - 1);
              that.setCaretPosition(that.textarea, posStart);
            } else {
              // 
              setTimeout(function () {
                that.setCaretPosition(that.textarea, len);
              }, 200);
            }
          }, 100);
        }
      }
    },
    updateMqJax () {
      let value = _outputRule(this.value);
      this.jax.innerText = '$' + value + '$';
      MathJax.Hub.Queue(["Typeset", MathJax.Hub, '_YkLatexEditor']);
      this.updateHistory(value);
    },
    updateHistory (latex) {
      if (latex && window.latexEditor.history) {
        window.latexEditor.history(latex)
          .then(list => {
            this.historyList = list;
            this.$nextTick(function () {
              MathJax.Hub.Queue(['Typeset', MathJax.Hub, '_YkLatexEditor']);
            });
          });
      } else {
        this.historyList = [];
      }
    },
    getSelectedText (obj) {
      var userSelection;
      var startPos = 0;
      var endPos = 0;
      if (typeof obj.selectionStart === 'number' && typeof obj.selectionEnd === 'number') {
        // IE
        // 
        startPos = obj.selectionStart,
        // 
        endPos = obj.selectionEnd;
      }
      return {
        start: startPos,
        end: endPos
      };
    },
    setCaretPosition (elem, index) {
      var len = this.value.length;
      // 
      if (len < index) return;
      setTimeout(function() {
        elem.focus()
        if (elem.setSelectionRange) { // 
          elem.setSelectionRange(index, index)
        } else { // IE9-
          var range = elem.createTextRange()
          range.moveStart("character", -len)
          range.moveEnd("character", -len)
          range.moveStart("character", index)
          range.moveEnd("character", 0)
          range.select()
        }
      }, 100)
    },
    filterValue (value) {
      let tempArr = value.split('');
      let count = 0;
      let newValue = tempArr.map(v => {
        if (WHITE_LIST.test(v)) {
          return v;
        } else {
          count++;
          return '';
        }
      }).join('');
      return {
        value: newValue,
        move: count
      };
    },
    hackerMouseLeave (evt) {
      if (evt.relatedTarget) {
        this.currentGroupIndex = this.currentLabelIndex = -1;
      }
    }
  },
  computed:{
    textarea () {
      return this.$refs.textarea;
    }
  }
}
</script>

