  background: rgba(220, 223, 230, 1);
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.7s;
}
.radio-circle {
  width: 18px;
  height: 18px;
  background-color: #fff;
  border-radius: 18px;
  transition: transform 0.7s;
}
.radio-box-active {
  background: rgba(38, 191, 105, 1);
}
.radio-box-active .radio-circle {
  transform: translateX(26px);
}
</style>



